// nav
// nav_logo
// nav_list , list_item

// 最外圍
.nav {
    display: flex;
    padding-left: 4%;
    padding-right: 4%;
    @include phoneV() {
        flex-direction: column;
        align-items: center;
        padding: 0px;
    }
}

// logo`
.nav_logo {
    display: inline-block;
    @include phoneV() {
        height: 50px;
    }
}

// menu
.nav_list {
    display: flex;
    // flex-wrap: wrap;
    @include phoneV() {
        flex-wrap: wrap;
    }
    .list_item {
        padding-top: 40px;
        padding-bottom: 40px;
        font-size: 20px;
        line-height: 30px;
        text-align: center;
        margin-left: 64px;
        @include pad() {
            margin-left: 40px;
        }
        @include phoneV() {
            padding: 8px;
            margin: 0px;
            width: 50%;
            border: 1px solid #ffffff;
        }
    }
    .list_item a {
        color: #fff;
    }
}

//mall
.nav_mall {
    max-width: 1400px;
    margin: 0 auto;
}
.nav_mall .nav_list {
    width: 100%;
    display: flex;
    justify-content: center;
    .list_item {
        flex: 1;
        margin: 0;
        padding: 0;
        border: 1px solid #DCDCDC;
        border-bottom:16px solid  transparent;
        a {
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 24px 0;
            padding-bottom: 8px;
            color: #000;
        }
        span {
            font-size: $fz * 4.2;
            line-height: 1.5;
        }
        img {
            height: 48px;
            margin-right: 8px;
        }
        &:hover,&.active{
        border-bottom:16px solid  #AA0601;
    }
    }
    
}
